.search-hjem{
    width: 100vw;
    height: 100vh;
    margin: 0px;
    padding: 0px;
    background:linear-gradient(to bottom, #484848,#9a9a9a,#dbdbdb);
}

.search-div{
    margin: 0px;
    padding: 0px;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items:end;
    justify-content: end;
}

.search-result{
    display: grid;
    width: 80vw;
    height: 450px;
    background:linear-gradient(to bottom, #5b6c75dc, #484848b3);
    border-radius: 15px;
    box-shadow: 0px 0px 19px 5px #52808e52;
    grid-template-rows: repeat(10,1fr);
    grid-template-columns: repeat(3,1fr);
}

.loading{
    width: 80vw;
    height: 450px;
    background:linear-gradient(to bottom, #ff0000dc, #ff0000b3);
    border-radius: 15px;
    box-shadow: 0px 0px 19px 5px #52808e52;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loading-text{
    font-size: 3em;
    font-weight: 700;
}